___CSS_LOADER_EXPORT___.push([module.id, `*
{
    box-sizing: border-box;
    margin : 0;
    padding : 0;
}

body
{
    background-image: linear-gradient(
        #0096C9 100%, 
        #2645bf 00% );

    width: 100%;
    height: 100%;
    min-height: calc(100vh);
}

.sign-up-success-container
{
    padding-top: 50px;
    padding-bottom: 200px;
}

.title
{
    display: flex;
    justify-content: center;
}

.title p
{
    color: white;
    text-decoration: none;
    background-color: #001E60;
    border-radius: 10px;
    padding : 10px;
    font-family: 'Poppins', sans-serif;
    font-weight : 600;
    font-size : clamp(2vw, 2.6vw, 2.6vw);
    text-align: center;
    margin-top : 5%;
}

.description
{
    display: flex;
    justify-content: center;
}

.description p
{
    color: white;
    text-decoration: none;
    background-color: #00468D;
    border-radius: 10px;
    padding : 10px;
    font-family: 'Poppins', sans-serif;
    font-weight : 500;
    font-size : clamp(1.4vw, 1.5vw, 1.5vw);
    text-align: center;
    margin-top : 3%;
    line-height: 45px;
}

.button-container
{
    text-align: center;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}

.button
{
    color: white;
    background-color: #001E60;
    font-size : clamp(1.6vw, 1.8vw, 1.8vw);
    font-family: 'Poppins', sans-serif;
    font-weight : 600;
    text-decoration: none;

    border-color : white;
    border-radius: 10px;
    cursor: pointer;
    padding: 10px 20px;
    margin-top : 4%;

    text-align: center;
    transition : all 0.3s ease-in-out;
}

.button:hover
{
    background-color :#00468D
}

@media all and (max-width : 780px)
{
    .title p
    {
        font-size : 20px;
        margin-top : 40px;
        margin-left : 40px;
        margin-right : 40px;
    }

    .description p
    {
        margin : 40px 10%;
        font-size : 16px;
        line-height: 30px;
    }

    .button
    {
        font-size : 20px;
    }
}`, "",{"version":3,"sources":["webpack://./src/pages/explore-services/signup-success.css"],"names":[],"mappings":"AAAA;;IAEI,sBAAsB;IACtB,UAAU;IACV,WAAW;AACf;;AAEA;;IAEI;;qBAEiB;;IAEjB,WAAW;IACX,YAAY;IACZ,uBAAuB;AAC3B;;AAEA;;IAEI,iBAAiB;IACjB,qBAAqB;AACzB;;AAEA;;IAEI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;;IAEI,YAAY;IACZ,qBAAqB;IACrB,yBAAyB;IACzB,mBAAmB;IACnB,cAAc;IACd,kCAAkC;IAClC,iBAAiB;IACjB,oCAAoC;IACpC,kBAAkB;IAClB,eAAe;AACnB;;AAEA;;IAEI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;;IAEI,YAAY;IACZ,qBAAqB;IACrB,yBAAyB;IACzB,mBAAmB;IACnB,cAAc;IACd,kCAAkC;IAClC,iBAAiB;IACjB,sCAAsC;IACtC,kBAAkB;IAClB,eAAe;IACf,iBAAiB;AACrB;;AAEA;;IAEI,kBAAkB;IAClB,cAAc;IACd,aAAa;IACb,uBAAuB;AAC3B;;AAEA;;IAEI,YAAY;IACZ,yBAAyB;IACzB,sCAAsC;IACtC,kCAAkC;IAClC,iBAAiB;IACjB,qBAAqB;;IAErB,oBAAoB;IACpB,mBAAmB;IACnB,eAAe;IACf,kBAAkB;IAClB,eAAe;;IAEf,kBAAkB;IAClB,iCAAiC;AACrC;;AAEA;;IAEI;AACJ;;AAEA;;IAEI;;QAEI,gBAAgB;QAChB,iBAAiB;QACjB,kBAAkB;QAClB,mBAAmB;IACvB;;IAEA;;QAEI,iBAAiB;QACjB,gBAAgB;QAChB,iBAAiB;IACrB;;IAEA;;QAEI,gBAAgB;IACpB;AACJ","sourcesContent":["*\n{\n    box-sizing: border-box;\n    margin : 0;\n    padding : 0;\n}\n\nbody\n{\n    background-image: linear-gradient(\n        #0096C9 100%, \n        #2645bf 00% );\n\n    width: 100%;\n    height: 100%;\n    min-height: calc(100vh);\n}\n\n.sign-up-success-container\n{\n    padding-top: 50px;\n    padding-bottom: 200px;\n}\n\n.title\n{\n    display: flex;\n    justify-content: center;\n}\n\n.title p\n{\n    color: white;\n    text-decoration: none;\n    background-color: #001E60;\n    border-radius: 10px;\n    padding : 10px;\n    font-family: 'Poppins', sans-serif;\n    font-weight : 600;\n    font-size : clamp(2vw, 2.6vw, 2.6vw);\n    text-align: center;\n    margin-top : 5%;\n}\n\n.description\n{\n    display: flex;\n    justify-content: center;\n}\n\n.description p\n{\n    color: white;\n    text-decoration: none;\n    background-color: #00468D;\n    border-radius: 10px;\n    padding : 10px;\n    font-family: 'Poppins', sans-serif;\n    font-weight 