{
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 18px;

    text-align: right;
    list-style: none;

    position: fixed;
    width: 100%;
    z-index: 9995;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.navigation-button
{
    display : inline-block;
    padding : 30px 20px;
}

.navigation-text
{
    color : white;
    text-decoration : underline;
    text-underline-offset: 8px;
    line-height: 10px;

    padding-left: 15px ;
    padding-right: 15px;
    padding-top: 5px;
    padding-bottom: 5px;

    transition : all 0.2s linear; 
    text-decoration: none;
}

.coming-soon {
    color: gray;
}

.nav-bar-wrapper {
    display: flex;
    flex-direction: column;
}

.navigation-menu-wrapper {
    width: 100vw;
    display: flex;
    justify-content: center;
    transition: background-color 0.5s ease;
}

#navigation-login
{
    color :#001E60;
    background-color: #ecaa1e;
    
    border-radius : 10px;

    padding : 15px 15px;
    margin-right : 20px;
    margin-left : 15px;
    text-decoration: none;
}

.navigation-text:hover
{
    color : #00468D;
}

#navigation-login:hover
{
    color : white;
    background-color :#00468D
}

.navigation-menu
{
    min-height : 45px;
    justify-content: space-evenly;
    align-items: center;
    display: flex;
    padding: 15px 25px 15px 25px;
    gap: 16px;
    transition: background-color 0.5s ease;
    width: 100%;
    max-width: 1600px;
}

.wrapper {
    transition: background-color 0.5s ease;
    max-width: none;
}

a > .navigation-menu {
    text-decoration: none;
}

#nav-menu-search-bar {
    flex-grow: 1;
    background-color: transparent;
    border: 2px solid white;
    border-radius: 50px;
    padding: 8px 15px;
    transition: background-color 1s ease;
    margin-right: 10px;

    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    color : white;
}

.scrolled {
    background-color: #001E60;
    transition: background-color 1s ease;
}

.scrolled > #nav-menu-search-bar {
    background-color: white;
    transition: background-color 1s ease;
    color : #001E60;
}

#nav-menu-search-bar::placeholder {
    color: white;
    opacity: 1;
}

#nav-menu-search-bar:-ms-input-placeholder{
    color: white;
}

#nav-menu-search-bar::-ms-input-placeholder{
    color: white;
}

.scrolled > #nav-menu-search-bar::placeholder {
    color: #001E60;

}

.scrolled > #nav-menu-search-bar:-ms-input-placeholder{
    color: #001E60;
}

.scrolled > #nav-menu-search-bar::-ms-input-placeholder{
    color: #001E60;
}

.navbar-logo {
    max-height: 55px;
    margin-right : 20px;
}

.navigation-hamburger
{
    display : none;
    cursor : pointer;
}

.navigation-line
{
    display : block;
    width : 25px;
    height : 3px;
    margin : 5px auto;
    transition : all 0.3s ease-in-out;
    background-color : white;
}

.navigation-sub-menu {
    opacity: 0;
    transition: opacity 1s ease;
    font-size: small;
    text-align: center;
    gap: 0;
    display: none;
}

.navigation-sub-menu.scrolled {
    opacity: 100;
}

.not-fixed {
    position: absolute;
    opacity: 100%;
}

.center-content {
    display: flex;
    justify-content: center;
    position: absolute;
    top: 8px;
    left: 0px;
    width: 100%;
}

.user-profile-image
{
    width : 80px;
    padding-left: 15px;
    padding-right: 15px;
    padding-top : 5px;
}

@media all and (max-width : 1200px)
{
    .navigation-bar
    {
        font-size: 16px;
    }

    .navigation-text
    {
        padding-left: 13px;
        padding-right: 13px;
    }

    .navigation-menu
    {
        gap: 0px;
    }

    #navigation-login
    {
        margin-right : 0px;
        padding: 10px 10px;
    }

    #nav-menu-search-bar
    {
        font-size: 15px;
        padding: 8px 12px;
    }
}

@media all and (max-width : 850px)
