        padding-right : 0;
        top: 7%;
        width: 40vw;

        transition : 0.3s ease-in-out;
        max-width: 100%;
        background-color: #001E60;
    }

    .navigation-button
    {
        margin-bottom : 15px;
        margin-top : 10px;
        padding : 15px 10px;
    }

