        margin-top: 40px;
        margin-bottom: 30px;
        background-color: white;
        transition: background-color 1s ease;
    }

    #nav-menu-search-bar::placeholder {
        color: black;
    }

    #nav-menu-search-bar:-ms-input-placeholder{
        color: black;
    }

    #nav-menu-search-bar::-ms-input-placeholder{
        color: black;
    }

    .not-fixed 
    {
        position: fixed;
        opacity: 100%;
    }

    .scrolled
    {
        background-color: transparent;
    }
}


@media all and (max-width : 560px)
{
    .navigation-bar
    {
        width: 60vw;
    }
}

