round-color: #00468D;\n    transition : all 0.3s ease-in-out;\n    position : relative;\n\n    margin-bottom : 20px;\n    margin-top : 20px;\n}\n\n.search-bar input\n{\n    background-color : transparent;\n    flex : 1;\n    border : 0;\n    outline : none;\n    padding : 0px 20px;\n    font-size : 20px;\n    color : whitesmoke;\n}\n\n::placeholder \n{\n    color : white;\n    transition: 300ms ease;\n}\n\n::placeholder:hover\n{\n    color : white;\n}\n\n.search-picture-container\n{\n    border : 0;\n    border-radius : 50%;\n    width : 40px;\n    height : 50px;\n    /* background: #00bfff; */\n    background : #00468D;\n    cursor : pointer;\n    transition : all 0.3s ease-in-out;\n}\n\n.search-picture\n{\n    width : 20px;\n    position: absolute;\n    right: 20px;\n    bottom: 20px;\n}\n\n.filter-container\n{\n    position : absolute;\n    top : 12%;\n    left : 72%;\n    width : 26%;\n\n    border-radius : 15px;\n\n    padding-top : 10px;\n    padding-bottom : 10px;\n    padding-left : 10px;\n    padding-right : 10px;\n\n    margin-right: 50px;\n\n    background-color:#00468D;\n    transition: 300ms ease ;\n}\n\ninput[type=\"checkbox\"]\n{\n    appearance: none;\n    -webkit-appearance: none;\n    background-color: aliceblue;\n    height : 15px;\n    width : 15px;\n    cursor : pointer;\n    border-radius : 10px;\n    outline : none;\n\n    align-items: center;\n    justify-content: space-around;\n}\n\ninput[type=\"checkbox\"]:hover\n{\n    background-color:#0096C9;\n}\n\ninput[type=\"checkbox\"]:checked\n{\n    background-color:#00bfff;\n}\n\ninput[type=\"checkbox\"]:checked:after\n{\n    display : block;\n}\n\n.filter-header\n{\n    color: white;\n    font-size : 22px;\n    font-family: 'Poppins', sans-serif;\n    font-weight: 400;\n    line-height: 50px;\n\n    margin-left : 35%;\n}\n\n.filter-list\n{\n    list-style: none;\n}\n\n.filter-item\n{\n    padding-top : 8px;\n    padding-bottom : 8px;\n    padding-left : 20px;\n    padding-right : 20px;\n}\n\n.filter-bubble\n{\n    transform:scale(1.3);\n}\n\n.filter-text\n{\n    color: white;\n    font-size : 18px;\n    font-family: 'Poppins', sans-serif;\n    font-weight : 300;\n    line-height: 50px;\n\n    padding-left : 10px;\n    cursor: pointer;\n}\n\n.filter-buttons\n{\n    padding-top : 10px;\n    padding-bottom : 20px;\n    padding-left : 20px;\n}\n\ninput[type=\"reset\"]\n{\n    font-family: 'Poppins', sans-serif;\n    color : white;\n    background: none;\n\tborder: none;\n    font-weight : 500;\n    font-size : 18px;\n    border-radius : 6px;\n    -webkit-border-radius : 6px;\n    cursor : pointer;\n}\n\n.filter-submit\n{\n    padding : 8px;\n}\n\n.filter-reset\n{\n    padding : 8px;\n    transform:translateX(50px);\n}\n\n.results\n{\n    display : flex;\n    justify-content : center;\n    flex-flow : wrap;\n    margin: 0px 20px;\n}\n\n.service-result-container\n{\n    background-color: #00468D;\n    color : white;\n    font-family: 'Poppins', sans-serif;\n    font-weight : 300;\n    margin : 40px 20px;\n    \n    border-radius: 20px;\n    width : 17.5rem;\n    cursor : pointer;\n\n    transition: 300ms ease ;\n    position : relative;\n}\n\n.service-result-container:hover\n{\n    background-color: #001E60;\n}\n\n.button-container\n{\n    text-align: center;\n    margin: 0 auto;\n    display: flex;\n    justify-content: center;\n}\n\n.service-button\n{\n    color: #ecaa1e;\n    font-size : 18px;\n    font-family: 'Poppins', sans-serif;\n    font-weight : 600;\n    text-decoration: none;\n\n    border-color : white;\n    border-radius: 10px;\n    cursor: pointer;\n    padding: 6px 10px;\n    margin-bottom : 15px;\n\n    position : absolute;\n    bottom : 0;\n    text-align: center;\n    transition : all 0.3s ease-in-out;\n}\n\n.service-button:hover\n{\n    color : white;\n    background-color :#00468D\n}\n\n.result-picture\n{\n    max-width: 100%;\n    max-height: 100%;\n    border-radius: 20px;\n}\n\n.result-title\n{\n    color : #ecaa1e;\n    margin-bottom : 15px;\n    margin-top: 15px;\n    font-weight: 600;\n    font-size: 23px;\n    display: flex;\n    text-align: center;\n    justify-content: center;\n    padding: 0px 20px;\n    height: 60px;\n    align-items: center;\n}\n\n.result-author\n{\n    margin-bottom : 70px;\n    font-size: 15px;\n    font-weight: 400;\n    padding: 0px 10px;\n\n    display: flex;\n    text-align: center;\n    justify-content: center;\n}\n\n.result-time\n{\n    margin-bottom : 3px;\n\n}\n\n.result-date\n{\n    margin-bottom : 3px;\n}\n\n.result-location\n{\n    margin-bottom: 50px;\n}\n\n.result-description\n{\n    line-height: 22px;\n    margin-bottom : 50px;\n}\n\n.search-result-container\n{\n    padding-bottom : 200px;\n    padding-top : 100px;\n}\n\n.loader-wrapper \n{\n  width: 100%;\n  height: 100%;\n  position: fixed;\n  top: 0;\n  left: 0;\n  background-color: #00468D;\n  display:flex;\n  justify-content: center;\n  align-items: center;\n  z-index: 999;\n}\n\n.loader \n{\n  display: inline-block;\n  width: 30px;\n  height: 30px;\n  position: relative;\n  border: 4px solid #Fff;\n  animation: loader 2s infinite ease;\n}\n\n.loader-inner \n{\n  vertical-align: top;\n  display: inline-block;\n  width: 100%;\n  background-color: #fff;\n  animation: loader-inner 2s infinite ease-in;\n}\n\n.loader--hidden\n{\n  opacity: 0;\n  visibility: hidden;\n}\n\n@keyframes loader \n{\n  0% { transform: rotate(0deg);}\n  25% { transform: rotate(180deg);}\n  50% { transform: rotate(180deg);}\n  75% { transform: rotate(360deg);}\n  100% { transform: rotate(360deg);}\n}\n\n@keyframes loader-inner \n{\n  0% { height: 0%;}\n  25% { height: 0%;}\n  50% { height: 100%;}\n  75% { height: 100%;}\n  100% { height: 0%;}\n}\n\n@media all and (max-width : 1700px)\n{\n    .filter-container\n    {\n        top : 12%;\n        left : 67%;\n        width : 30%;\n    }\n\n    .filter-text\n    {\n        font-size : 17px;\n        line-height: 45px;\n    \n    }\n}\n\n@media all and (max-width : 1380px)\n{\n    .service-result-container\n    {\n        width: 16rem;\n    }\n\n    .result-title\n    {\n        font-size : 20px;\n    }\n\n    .result-author\n    {\n        margin-bottom: 60px;\n    }\n\n    .service-button\n    {\n        font-size : 16px;\n    }\n\n    .search-bar input\n    {\n        font-size: 18px;\n    }\n    \n}\n\n@media all and (max-width : 988px)\n{\n    .results\n    {\n        justify-content: center;\n        margin: 0px 0px;\n    }\n\n    .service-result-container\n    {\n        width: 14rem;\n    }\n\n    .result-title\n    {\n        font-size: 18px;\n    }\n\n    .search-bar\n    {\n        margin-top : 20px;\n        margin-right: 25px;\n        margin-left: 25px;\n        padding: 0px 10px;\n    }\n    \n    .search-bar input\n    {\n        font-size: 16px;\n    }\n    \n    .search-picture\n    {\n        bottom : 17px;\n    }\n\n    .service-button\n    {\n        font-size : 15px;\n    }\n}\n\n@media all and (max-width : 800px)\n{\n    .results\n    {\n        margin: 0px 0px;\n    }\n\n    .service-result-container \n    {\n        width: 16rem;\n        margin: 30px 15px;\n    }\n\n    .result-title\n    {\n        font-size : 20px;\n        margin-top: 10px;\n        margin-bottom : 10px;\n        padding: 10px 10px;\n    }\n\n    .result-author\n    {\n        font-size : 15px;\n    }\n\n    .service-button\n    {\n        font-size : 14px;\n    }\n\n}\n\n@media all and (max-width : 600px)\n{\n    .search-bar input\n    {\n        padding: 0px 10px;\n        font-size: 16px;\n    }\n\n    .search-picture-container\n    {\n        width : 50px;\n    }\n\n    .result-text-container\n    {\n        display: flex;\n        justify-content: center;\n        flex-wrap: wrap;\n        width: 100%;\n    }\n\n    .service-result-container\n    {\n        margin : 20px 15px;\n        font-size : 14px;\n        padding: 0;\n        width : 90%;\n        display : flex;\n        align-items: center;\n    }\n\n    .result-picture\n    {\n        max-