    position: relative;
    overflow-x: hidden;
}

@keyframes fadeIn 
{
    0% { opacity: 0;
        transform : translatey(10px); }
    100% { opacity: 1;
        transform : translatey(0px); }
}

.header
{
    background: linear-gradient(
        to bottom,
        rgba(0, 0, 0, .5),
        rgba(0, 40, 96, 0.5),
        rgba(0, 70, 141, 0.5),
        rgba(0, 60, 96, 0.99)
     );

    background-repeat : no-repeat;
    min-height: 130vh;
    max-height: 130vh;
    position: relative;
    overflow : hidden;
}

.header-container
{
    display: flex;
    flex-direction: column;
}

#header-title
{
    color: white;
    font-size : clamp(36px, 4vw, 60px);
    font-weight: 400;
    font-family: 'Poppins', sans-serif;
    line-height: 110px;
    text-shadow: 0px 2px 3px rgb(0 0 0 / 50%);

    display : flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position : relative;
    animation: fadeIn 1s;
    height : 50vh;
    margin-top : 5%;
    color: #fff;
}

#header-subtitle
{
    color: white;
    font-size : 26px;
    font-family: 'Poppins', sans-serif;
    text-align: center;
    text-shadow: 0px 3px 3px rgb(0 0 0 / 80%);
    animation: fadeIn 1s;

    margin : auto;
    margin-top : auto;
    justify-content: center;
    align-items: center;
    position : absolute;
    bottom : 28%;
    left: 2%;
    right: 2%;
}

#header-button
{
    width : 100%;
    text-align: center;
    display : flex;
    justify-content: center;
    animation: fadeIn 1s;
}

#button
