    text-decoration: none;

    background-color : #ecaa1e;
    border-color : white;
    border-radius: 10px;
    cursor: pointer;

    position : absolute;
    padding : 10px 40px;
    transition : all 0.3s ease-in-out;
}

#button:hover
{
    color : white;
    background-color :#00468D;
}

.header-curve
{
    position : absolute;
    bottom : 0;
    left : 0;
    width : 100%;
}

