    font-family: 'Poppins', sans-serif;
    font-size: clamp(18px, 3vw, 40px);
    text-align: center;
}

.main-container
{
    position: relative;
    margin-top : 20px;
}

.categories-container
{
    position : relative; 
    width : 100%;
    height : 100%;
    display : flex;
    justify-content: center;
    padding-right : 5%;
    padding-left : 5%;
}

.category-box  
{
    position : relative;
    width : 21%;
    background-size: cover;
    float : left;
    margin : 1% 2%;
    box-sizing : border-box;
    overflow : hidden;
    border-radius : 10px;
    cursor: pointer;
    transition: 300ms ease ;
    text-decoration: none;
}

.box
{
    position: relative;
    text-align: center;
}

.box img
{
    max-width: 100%;
    max-height: 100%;
}

#box1:hover
{
    transform: scale(1.05);
}

#box2:hover
{
    transform: scale(1.05);
}

.box-header-text
{
    color : white;
    text-align: center;
    text-decoration: none;
    font-family: 'Poppins', sans-serif;
    font-weight : 500;
    font-size: 2.5vw;
    margin-top : 20px;
    text-shadow: 2px 1px 8px rgb(0 0 0 / 100%);
    background-color: rgba(0, 0, 0, 0.3);
}

.preview-text
