___CSS_LOADER_EXPORT___.push([module.id, `footer 
{
    background-color: #00468D;
    color: white;
    font-family: 'Poppins', sans-serif;
    text-align: center;
    font-size: 14px;
    position: absolute;
    width: 100%;
    padding : 20px 10%;
}

.footer-text
{
    margin : 20px 10px;
}

.footer-container
{
    display: flex;
    align-content: center;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
}

.footer-container a
{
    color : white;
    margin : 10px 20px;
    text-decoration: none;
    padding-top: 5px;
}

#tech-support
{
    color : #00468D;
    transition : all 0.3s ease-in-out;
    font-weight: 500;
    background-color: #ecaa1e;
    padding : 10px 12px;
    border-radius : 15px;
}

#tech-support:hover
{
    color: white;
    background-color: #001E60;
}

@media all and (max-width : 480px)
{
    footer
    {
        padding : 10px 0%;
        font-size : 12px;
    }
}

@media all and (max-width : 360px)
{
    .footer-container a
    {
        margin : 12px 40px;
    }
}
