___CSS_LOADER_EXPORT___.push([module.id, `.container-login
{
  z-index: 9999;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  background: white;
  border-radius: 10px;
  box-shadow: 10px 10px 15px rgba(0,0,0,0.05);
}
.container-for-login h1
{
  text-align: center;
  padding: 20px 0;
  border-bottom: 1px solid silver;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 34px;
}

.container-for-login 
{
  display: flex;
  flex-direction: column;
}

.container-for-login form
{
  padding: 0 40px;
  box-sizing: border-box;
}

form .text-field
{
  position: relative;
  border-bottom: 2px solid #adadad;
  margin: 30px 0;
  font-family: 'Poppins', sans-serif;
}

.text-field-input
{
  width: 100%;
  padding: 0 5px;
  height: 40px;
  font-size: 16px;
  border: none;
  background: none;
  outline: none;
  font-family: 'Poppins', sans-serif;
}

.text-field-label
{
  position: absolute;
  top: 50%;
  left: 5px;
  color: #adadad;
  transform: translateY(-50%);
  font-size: 16px;
  pointer-events: none;
  transition: .5s;
  font-family: 'Poppins', sans-serif;
}

.text-field-span::before
{
  content: '';
  position: absolute;
  top: 40px;
  left: 0;
  width: 0%;
  height: 2px;
  background: #2691d9;
  transition: .5s;
}

.text-field-input:focus ~ label,
.text-field-label.focused-field
{
  top: -5px;
  color: #2691d9;
}

.text-field-input:focus ~ span::before,
.text-field-span.focused-field
{
  width: 100%;
}

.pass
{
  margin: -5px 0 20px 5px;
  color: #a6a6a6;
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
}

.pass:hover
{
  text-decoration: underline;
}

.login-submit-button
{
  width: 100%;
  height: 50px;
  border: 1px solid;
  background: #2691d9;
  border-radius: 25px;
  font-size: 18px;
  color: #e9f4fb;
  font-weight: 700;
  cursor: pointer;
  outline: none;
  font-family: 'Poppins', sans-serif;
}

#login input[type="submit"]:hover
{
  border-color: #2691d9;
  transition: .5s;
}

.signup_link
{
  margin: 30px 0;
  text-align: center;
  font-size: 16px;
  color: #666666;
  font-family: 'Poppins', sans-serif;
}

.signup_link a
{
  color: #2691d9;
  text-decoration: none;
  font-family: 'Poppins', sans-serif;
}

.signup_link a:hover
{
  text-decoration: underline;
}

#delete-header
{
  font-size : 25px;
  padding : 20px;
  margin-bottom : 20px;
}

#login-submission
{
  width: 100%;
  height: 50px;
  border: 1px solid;
  background: #001E60;
  border-radius: 25px;
  font-size: 18px;
  color: #ecaa1e;
  font-weight: 700;
  cursor: pointer;
  outline: none;
  font-family: 'Poppins', sans-serif;
  border : none;
  transition: 300ms ease;
}

#login-submission:hover
{
  color: #001E60;
  background: #ecaa1e;

}


@media all and (max-width : 450px)
{

  .container-login
  {
    width : 300px;
  }

  .container form
  {
    padding: 0 15px;
  }
}

#login-popup-background {
  background-color:rgb(14, 14, 30);
  opacity: 50%;
  z-index: 9997;
  position: fixed;
  width: 100vw;
  height: 100vh;
}

.hidden {
  display: none;
  visibility: hidden;
}`, "",{"version":3,"sources":["webpack://./src/components/login.css"],"names":[],"mappings":"AAEA;;EAEE,aAAa;EACb,eAAe;EACf,QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,YAAY;EACZ,iBAAiB;EACjB,mBAAmB;EACnB,2CAA2C;AAC7C;AACA;;EAEE,kBAAkB;EAClB,eAAe;EACf,+BAA+B;EAC/B,kCAAkC;EAClC,gBAAgB;EAChB,eAAe;AACjB;;AAEA;;EAEE,aAAa;EACb,sBAAsB;AACxB;;AAEA;;EAEE,eAAe;EACf,sBAAsB;AACxB;;AAEA;;EAEE,kBAAkB;EAClB,gCAAgC;EAChC,cAAc;EACd,kCAAkC;AACpC;;AAEA;;EAEE,WAAW;EACX,cAAc;EACd,YAAY;EACZ,eAAe;EACf,YAAY;EACZ,gBAAgB;EAChB,aAAa;EACb,kCAAkC;AACpC;;AAEA;;EAEE,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,cAAc;EACd,2BAA2B;EAC3B,eAAe;EACf,oBAAoB;EACpB,eAAe;EACf,kCAAkC;AACpC;;AAEA;;EAEE,WAAW;EACX,kBAAkB;EAClB,SAAS;EACT,OAAO;EACP,SAAS;EACT,WAAW;EACX,mBAAmB;EACnB,eAAe;AACjB;;AAEA;;;EAGE,SAAS;EACT,cAAc;AAChB;;AAEA;;;EAGE,WAAW;AACb;;AAEA;;EAEE,uBAAuB;EACvB,cAAc;EACd,eAAe;EACf,kCAAkC;AACpC;;AAEA;;EAEE,0BAA0B;AAC5B;;AAEA;;EAEE,WAAW;EACX,YAAY;EACZ,iBAAiB;EACjB,mBAAmB;EACnB,mBAAmB;EACnB,eAAe;EACf,cAAc;EACd,gBAAgB;EAChB,eAAe;EACf,aAAa;EACb,kCAAkC;AACpC;;AAEA;;EAEE,qBAAqB;EACrB,eAAe;AACjB;;AAEA;;EAEE,cAAc;EACd,kBAAkB;EAClB,eAAe;EACf,cAAc;EACd,kCAAkC;AACpC;;AAEA;;EAEE,cAAc;EACd,qBAAqB;EACrB,kCAAkC;AACpC;;AAEA;;EAEE,0BAA0B;AAC5B;;AAEA;;EAEE,gBAAgB;EAChB,cAAc;EACd,oBAAoB;AACtB;;AAEA;;EAEE,WAAW;EACX,YAAY;EACZ,iBAAiB;EACjB,mBAAmB;EACnB,mBAAmB;EACnB,eAAe;EACf,cAAc;EACd,gBAAgB;EAChB,eAAe;EACf,aAAa;EACb,kCAAkC;EAClC,aAAa;EACb,sBAAsB;AACxB;;AAEA;;EAEE,cAAc;EACd,mBAAmB;;AAErB;;;AAGA;;;EAGE;;IAEE,aAAa;EACf;;EAEA;;IAEE,eAAe;EACjB;AACF;;AAEA;EACE,gCAAgC;EAChC,YAAY;EACZ,aAAa;EACb,eAAe;EACf,YAAY;EACZ,aAAa;AACf;;AAEA;EACE,aAAa;EACb,kBAAkB;AACpB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&family=Poppins:wght@400;500;600&display=swap');\n\n.container-login\n{\n  z-index: 9999;\n  position: fixed;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 400px;\n  background: white;\n  border-radius: 10px;\n  box-shadow: 10px 10px 15px rgba(0,0,0,0.05);\n}\n.container-for-login h