{
    box-sizing: border-box;
    margin : 0;
    padding : 0;
    scroll-behavior: smooth;
}

body
{
    background: #0096C9;
    width: 100%;
    height: 100%;
    min-height: calc(100vh);
    position: relative;
}

.service-categories-container
{
    padding-top: 120px;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.category-container
{
    position: relative;
    text-decoration: none;
    width: 40%;
    height: 100%;
    margin: 2%;
    border-radius: 15px;
    background-color: #00468D;
    cursor: pointer;
    transition: 300ms ease;
    padding: 40px;
}

.category-container-unselectable
{
    position: relative;
    text-decoration: none;
    width: 40%;
    height: 100%;
    margin: 2%;
    border-radius: 15px;
    background-color: #002b57;
    transition: 300ms ease;
    padding: 40px;
}

.category-container-title
{
    width : 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom : 30px;
    background-color: #001E60;
    border-radius: 10px;
    padding : 15px;
    color: white;
    font-family: 'Poppins', sans-serif;
    text-align: center;
    font-weight: 400;
    font-size: 30px;
}

.category-container:hover
{
    background-color: #001E60;
}

.category-header
{
    color: white;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 32px;
}

.category-text
{
    color: white;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    font-size: 14px;
    margin-top: 30px;
    line-height: 20px;
}

@media all and (max-width : 820px)
{
    .category-container
    {
        width: 80%;
        margin: 5% 2%;
        padding: 30px;
    }

    .category-header
    {
        font-size: 28px;
    }
}

@media all and (max-width : 480px)
{
    .category-container
    {
        width: 85%;
        margin: 6% 2%;
        padding: 25px;
    }

    .category-container-title
    {
        font-size: 25px;
    }

    .category-header
    {
        font-size: 25px;
    }

    .category-text
    {
        font-size: 13px;
        margin-top: 20px;
    }

